<template lang="html">

  <section class="login">
    <h1>login</h1>
    <form>
      <input type="email" id="login" class="fadeIn second" name="login" placeholder="email" v-model="email"><br>
      <input type="password" id="password" class="fadeIn third" name="login" placeholder="password" v-model="cont"><br>
      <button class="btn btn-outline-primary" v-on:click="aut">Login</button>
      <p>¿Aun no tienes una cuenta? <a class="nav-link" href="#"><router-link to="/registro">Regístrate</router-link></a></p>
      <p>Para añadir productos al carrito debes loguearte</p>
    </form>

  </section>

</template>

<script lang="js">
import firebase from 'firebase'
  export default  {
    name: 'login',
    props: [],
    mounted () {

    },
    data () {
      return {
        email:"",
        cont:""        
      }
    },
    methods: {
      aut() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.cont).then(()=>{
          this.$router.replace('/privado');
        })
        .catch(function(error) {
        //var errorCode = error.code;
        //var errorMessage = error.message;
        console.log(error);        
        alert("Usuario o contraseña incorrecta");
      });
      }
    },
    computed: {

    }
}


</script>

